import { Component, OnInit } from '@angular/core';
// rxjs
import { forkJoin, ReplaySubject } from 'rxjs';
import { filter, map, takeUntil } from 'rxjs/operators';
// centerService
import { CenterService } from '../../center.service'
@Component({
  selector: 'app-parking-kpi',
  templateUrl: './parking-kpi.component.html',
  styleUrls: ['./parking-kpi.component.scss']
})
export class ParkingKpiComponent implements OnInit {
  usage: any;
  premium: any;
  premium_up: any;
  premium_down: any;
  income: any;
  constructor(private centerService: CenterService,) { }
  private destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);
  ngOnInit(): void {
    setTimeout(() => {
      //loading
      this.centerService.loading$.next(false);
    }, 500);
    this.centerService.estimate_result$.pipe(
      takeUntil(this.destroyed$)
    ).subscribe({
      next: d => {
        this.usage = d[0].toFixed(2);
        this.income = Math.round(d[1]).toLocaleString();
        this.premium = Math.round(d[2])
        this.premium_up = Math.round(d[2] + 1.96 * (614486 / Math.sqrt(2248)))
        this.premium_down = Math.round(d[2] - 1.96 * (614486 / Math.sqrt(2248)))



      },
      error: (err) => {
        console.log(err);
      }
    })
  }
  /********Function*********/
  btnStatus(status: number) {
    if (status == 2) {
      this.centerService.estimate_btn$.next(status);
    }
  }
}
