import { Component, OnInit } from '@angular/core';
// rxjs
import { forkJoin, ReplaySubject } from 'rxjs';
import { filter, map, takeUntil } from 'rxjs/operators';
import { MatSnackBar } from '@angular/material/snack-bar';
// centerService
import { CenterService } from '../../center.service';
import { Router } from '@angular/router';
import { routes } from './../../routes';
@Component({
  selector: 'app-parking-estimate',
  templateUrl: './parking-estimate.component.html',
  styleUrls: ['./parking-estimate.component.scss'],
})
export class ParkingEstimateComponent implements OnInit {
  default_data: { 1: any[]; 2: any[]; 3: any[]; 4: [] };
  density: any;
  space_id: string[];
  population: any;
  station: number;
  parkingLot: number;
  parkingGrid: number;
  monthlyRate: any;
  totalExpenses: any;
  monthlyRentalRate: any;
  defaultGrid: any;
  x9: number;
  x10: number;
  x11: number;
  x12: number;
  x13: number;
  x14: number;
  x15: number;
  x16: number;
  x17: number;
  x3: any;
  x2: any;
  workday: any;
  holiday: any;
  constructor(
    private centerService: CenterService,
    private snackBar: MatSnackBar,
    private router: Router,
  ) { }
  public routers: typeof routes = routes;
  private destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);
  //
  districtPanel: boolean = false;
  parkingFormtPanel: boolean = false;
  ratePanel: boolean = false;
  parkingTypePanel: boolean = false;
  areaPanel: boolean = false;
  weekdayPanel: boolean = false;
  holidayPanel: boolean = false;
  mothRatePlaceholder = ""
  weekdayPlaceholder = ''
  weekendPlaceholder = ''
  source: number = NaN
  // 
  district_task = [
    { name: "", value: 1 },
    { name: "", value: 2 },
    { name: "", value: 3 },
    { name: "", value: 4 },
    { name: "", value: 5 },
    { name: "", value: 6 },
    { name: "", value: 7 },
    { name: "", value: 8 },
    { name: "", value: 9 },
    { name: "", value: 10 },
    { name: "", value: 11 },
    { name: "", value: 12 },
    { name: "", value: 13 },
    { name: "", value: 14 },
    { name: "", value: 15 },
    { name: "", value: 16 },
    { name: "", value: 17 },
    { name: "", value: 18 },
    { name: "", value: 19 },
    { name: "", value: 20 },
    { name: "", value: 21 },
    { name: "", value: 22 },
    { name: "", value: 23 },
    { name: "", value: 24 },
    { name: "", value: 25 },
    { name: "", value: 26 },
    { name: "", value: 27 },
    { name: "", value: 28 },
    { name: "", value: 29 },
  ]
  //
  parking_form = [
    { name: '' },
    { name: '' }
  ]
  selectedType: any
  //
  parking_type = [
    { name: '' },
    { name: '' },
    { name: '' },
    { name: '' }
  ]
  //
  tooltip = "(53%~100%)\n (20%~53%) \n(0%~20%)"
  //
  areas = [
    { name: '' },
    { name: '' },
  ]
  //
  week_rate = [
    { value: 10 },
    { value: 20 },
    { value: 30 },
    { value: 40 },
    { value: 50 }
  ]
  //
  holiday_rate = [
    { value: 10 },
    { value: 20 },
    { value: 30 },
    { value: 40 },
    { value: 50 }
  ]

  ngOnInit(): void {

    // id
    this.centerService.returnMapSelectId$.pipe(
      takeUntil(this.destroyed$)
    ).subscribe({
      next: d => {
        console.log(d);
        // 
        this.population = 0
        this.parkingLot = 0
        this.station = 0
        this.parkingGrid = 0
        this.space_id = d
        // 
        // this.space_id = [...new Set(d)]
        this.space_id.forEach(e => {

          if (typeof (e) == 'string') {
            if ((e.split('on_street')).length > 1) {
              this.parkingGrid++
            } else {
              this.default_data[3].forEach(d => {

                if (e === d.geohash) {
                  this.population = this.population + d.count / 4
                }

              })
            }

          } else {

            if (e > 500) { // 
              // console.log(e);
              this.parkingLot++
            } else {
              // 
              this.station++
            }

          }

        })
        // console.log('', this.population);
        this.x3 = this.population
        this.population = Math.round(this.population).toLocaleString()
      },
      error: (err) => {
        console.log(err);
      }
    })

    // 
    this.centerService.returnMapSelectParking$.pipe(
      takeUntil(this.destroyed$)
    ).subscribe({
      next: d => {
        console.log(d);
        // d[0]: d[1]: d[2]:
        this.weekdayPlaceholder = '' + d[0] + '/'
        this.weekendPlaceholder = '' + d[1] + '/'
        this.mothRatePlaceholder = '' + d[2] + ''

      },
      error: (err) => {
        console.log(err);
      }
    })

    setTimeout(() => {
      //loading
      this.centerService.loading$.next(false);
    }, 500);
    this.getApi()
  }
  ngAfterContentInit(): void {
    //Called after ngOnInit when the component's or directive's content has been initialized.
    //Add 'implements AfterContentInit' to the class.
    this.startMapSelection()
  }
  parkingtype_change(type) {

    if (type.option._value == '') {
      this.x9 = 1
      this.x10 = 0
      this.x11 = 0
    }
    else if (type.option._value == '') {
      this.x9 = 0
      this.x10 = 1
      this.x11 = 0
    } else if (type.option._value == '') {
      this.x9 = 0
      this.x10 = 0
      this.x11 = 1
    } else if (type.option._value == '') {
      this.x9 = 0
      this.x10 = 0
      this.x11 = 0
    }

  }
  parkingform_change(type) {
    if (type.option._value == '')
      this.selectedType = 0
    else
      this.selectedType = 1
  }
  rate_change(e) {
    console.log(e.option._value);
  }
  workdayCharge(charge) {
    this.workday = charge.option._value
    if (charge.option._value == '20') {
      this.x12 = 1
      this.x13 = 0
      this.x14 = 0
    } else if (charge.option._value == '30') {
      this.x12 = 0
      this.x13 = 1
      this.x14 = 0
    } else if (charge.option._value == '40') {
      this.x12 = 0
      this.x13 = 0
      this.x14 = 1
    } else {
      this.x12 = 0
      this.x13 = 0
      this.x14 = 0
    }
  }
  holidayCharge(charge) {
    this.holiday = charge.option._value
    if (charge.option._value == '20') {
      this.x15 = 1
      this.x16 = 0
      this.x17 = 0
    } else if (charge.option._value == '30') {
      this.x15 = 0
      this.x16 = 1
      this.x17 = 0
    } else if (charge.option._value == '40') {
      this.x15 = 0
      this.x16 = 0
      this.x17 = 1
    } else {
      this.x15 = 0
      this.x16 = 0
      this.x17 = 0
    }
  }
  download() {
    this.centerService.estimate_btn$.next(1);
    console.log(this.density);
  }
  /********Function*********/
  btnStatus(status: number) {
    console.log(this.monthlyRate);
    // token
    this.centerService.post('/api/users/get_session', {}).subscribe({
      // next: () => {
      // },
      error: () => {
        this.snackBar.open('', '', {
          duration: 10000,
          horizontalPosition: 'center',
          verticalPosition: 'top',
        });
        // 
        this.router.navigate([this.routers.LOGIN]).then();
      },
    });
    let usage, income, premium
    let x = {
      X1: this.monthlyRentalRate,
      X2: parseInt(this.density),
      X3: parseInt(this.population),
      X4: this.parkingLot,
      X5: this.station,
      X6: this.parkingGrid,
      X7: this.monthlyRate,
      X8: this.selectedType,
      X9: this.x9,
      X10: this.x10,
      X11: this.x11,
      X12: this.x12,
      X13: this.x13,
      X14: this.x14,
      X15: this.x15,
      X16: this.x16,
      X17: this.x17,
    }
    if (status == 1) {
      if (x.X1 != undefined && x.X2 != undefined && x.X3 != undefined && x.X4 != undefined && x.X5 != undefined
        && x.X6 != undefined && x.X7 != undefined && x.X8 != undefined && x.X9 != undefined && x.X10 != undefined
        && x.X11 != undefined && x.X12 != undefined && x.X13 != undefined && x.X14 != undefined && x.X15 != undefined
        && x.X16 != undefined && x.X17 != undefined) {

        console.log(x, this.density, this.population);
        let monthlyRentalType
        if (this.monthlyRate >= 53 && this.monthlyRate <= 100) {
          monthlyRentalType = '/api/estimate/estimate_high'
        } else if (this.monthlyRate >= 20 && this.monthlyRate < 53) {
          monthlyRentalType = '/api/estimate/estimate_mid'
        } else if (this.monthlyRate >= 0 && this.monthlyRate < 20) {
          monthlyRentalType = '/api/estimate/estimate_low'
        }

        this.centerService.post(monthlyRentalType,
          {
            X1: this.monthlyRentalRate,
            X2: this.x2,
            X3: this.x3,
            X4: this.parkingLot,
            X5: this.station,
            X6: this.parkingGrid,
            X7: this.monthlyRate,
            X8: this.selectedType,
            X9: this.x9,
            X10: this.x10,
            X11: this.x11,
            X12: this.x12,
            X13: this.x13,
            X14: this.x14,
            X15: this.x15,
            X16: this.x16,
            X17: this.x17,
          }).subscribe((e) => {
            console.log(monthlyRentalType,e);
            usage = e.data
            income = this.defaultGrid * this.monthlyRentalRate * this.monthlyRate / 100 * 12 + this.defaultGrid * (1 - this.monthlyRate / 100) * (usage / 100) * ((+this.workday) * 24 * 249 + (+this.holiday) * 24 * 116)
            // premium = -11410.376 + 0.554 * income + 0.135 * this.totalExpenses
            premium = 0.92 * income - this.totalExpenses
            this.centerService.estimate_result$.next([usage, income, premium]);
          })
        this.centerService.estimate_btn$.next(status);
      } else {
        console.log(x);
        this.snackBar.open('', '', {
          horizontalPosition: 'center',
          verticalPosition: 'bottom',
          duration: 5000
        });
      }


    }
  }
  district_change(district) {
    // 
    for (let i = 0; i < this.default_data['2'].length; i++) {
      if (this.default_data['2'][i].site_id == '' + district.option._value) {
        this.density = this.default_data['2'][i].population_density
      }
    }
    this.x2 = this.density
    // this.density = this.density.toLocaleString()
  }
  // 
  startMapSelection() {
    // 
    this.centerService.mapSelectType$.next(-2)
  }
  getApi() {
    this.default_data = { 1: [], 2: [], 3: [], 4: [] };
    // 
    const post1 = this.centerService.post('/api/estimate/parkingLot', {}).pipe(
      map(val => {
        val.data.forEach((e) => {
          e.source = 1;
        });
        return { ...val, source: 1 }
      })
    )
    // 
    const post2 = this.centerService.post('/api/estimate/population_density', {}).pipe(
      map(val => {
        val.data.forEach((e) => {
          e.source = 2;
        });
        return { ...val, source: 2 }
      })
    )
    // 
    const post3 = this.centerService.post('/api/estimate/population', {}).pipe(
      map(val => {
        val.data.forEach((e) => {
          e.source = 3;
        });
        return { ...val, source: 3 }
      })
    )
    // 
    const post4 = this.centerService.post('/api/estimate/station', {}).pipe(
      map(val => {
        val.data.forEach((e) => {
          e.source = 4;
        });
        return { ...val, source: 4 }
      })
    )
    forkJoin([post1, post2, post3, post4]).pipe(
      takeUntil(this.destroyed$)
    ).subscribe(
      (d) => {
        // 
        d.forEach(e => {
          if (e.code !== 'success') { // 
            if (e.source === 2) {
              this.snackBar.open(' ', '', {
                horizontalPosition: 'center',
                verticalPosition: 'top',
                duration: 1000
              });
            } else if (e.source === 3) {
              this.snackBar.open(' ', '', {
                horizontalPosition: 'center',
                verticalPosition: 'top',
                duration: 1000
              });
            }
          } else {
            this.default_data[e.source] = e.data
          }
        });
        console.log(this.default_data);
      },
      (err) => {
        console.log(err);
      }
    )
  }
}
