  /*==================================
    Function
  ==================================*/
  // id
  getId() {
    this.centerService.loading$.next(true)
    let sd_total = 0
    let off_steet_total = 0
    let on_street_total = 0
    // 
    const source1 = this.centerService.post('/api/parking/sd_id', {}).pipe(
      map(val => {
        sd_total = val.data.length
        return { ...val, source: 1 }
      })
    )
    const source2 = this.centerService.post('/api/parking/off_street_id', {}).pipe(
      map(val => {
        console.log(val);
        off_steet_total = val.data.length;
        return { ...val, source: 2 }
      })
    )
    const source3 = this.centerService.post('/api/parking/on_street_id', {}).pipe(
      map(val => {
        on_street_total = val.data.length;
        return { ...val, source: 3 }
      })
    )
    forkJoin([source1, source2, source3]).pipe(
      takeUntil(this.destroyed$)
    ).subscribe(

      (d) => {
        let i = 0
        // 
        d.forEach(e => {
          if (e.code !== 'success') { // 
            if (e.source === 1) {
              this.snackBar.open('ID', '', {
                horizontalPosition: 'center',
                verticalPosition: 'top',
                duration: 1000
              });
            } else if (e.source === 2) {
              this.snackBar.open('ID', '', {
                horizontalPosition: 'center',
                verticalPosition: 'top',
                duration: 1000
              });
            } else if (e.source === 3) {
              this.snackBar.open('ID', '', {
                horizontalPosition: 'center',
                verticalPosition: 'top',
                duration: 1000
              });
            }
            this.centerService.loading$.next(false)
          } else { // 
            if (e.source === 1) {
              console.log(e.data);
              for (const f of e.data) {
                this.sd_id[f.district].push({ name: f.id, completed: true, value: { v: f.id, d: f.district } })
              }
            } else if (e.source === 2) {
              for (const f of e.data) {
                this.off_street_id[f.district].push({ name: f.id + f.name, completed: true, value: { v: f.id, d: f.district } })
              }
            } else if (e.source === 3) {
              for (const f of e.data) {
                this.on_street_id[f.district].push({ name: f.id + f.name, completed: true, value: { v: f.id, d: f.district } })
              }
            }
          }
        })
        this.centerService.total_id$.next({
          "sd": sd_total,
          "off_street": off_steet_total,
          "on_street": on_street_total,
        })
        this.centerService.loading$.next(false)
      }

    )


  }
