  // 
  setAll2(completed: boolean) {
    console.log(this.source);
    if (this.source == 1) {
      this.district_task.completed = completed;
      if (this.district_task.subtasks == null) {
        return;
      }
      let taskBool = completed
      const taskArray = []
      const idArray = []
      this.district_task.subtasks.map(t => {
        if (t.name == "" ||
          t.name == "" ||
          t.name == "" ||
          t.name == "" ||
          t.name == "" ||
          t.name == "" ||
          t.name == "" ||
          t.name == "" ||
          t.name == "" ||
          t.name == "" ||
          t.name == "" ||
          t.name == "" ||
          t.name == "" ||
          t.name == "" ||
          t.name == "" ||
          t.name == ""
        ) {
          t.completed = completed
          if (completed) {
            taskArray.push(t.name)
            idArray.push(t.value)
          }
        }
      })
      if (taskBool) {
        this.picked_district = idArray
        this.spaceChangeByDistrict(idArray)
        this.expansion2_text = taskArray.toString()
      } else {
        this.picked_district = []
        this.space = [] // 
        this.expansion2_text = ''
      }
    } else {
      this.district_task.completed = completed;
      if (this.district_task.subtasks == null) {
        return;
      }
      let taskBool = completed
      const taskArray = []
      const idArray = []
      this.district_task.subtasks.map(t => {
        t.completed = completed
        if (completed) {
          taskArray.push(t.name)
          idArray.push(t.value)
        }
      })
      if (taskBool) {
        this.picked_district = idArray
        this.spaceChangeByDistrict(idArray)
        this.expansion2_text = taskArray.toString()
      } else {
        this.picked_district = []
        this.space = [] // 
        this.expansion2_text = ''
      }
    }

  }
  // 
  setCompleted2(i: number) {
    this.district_task.subtasks[i].completed = !this.district_task.subtasks[i].completed
    console.log(this.district_task);
    let taskBool = false
    const taskArray = []
    const idArray = []
    this.district_task.subtasks.map(t => {
      if (t.completed) {
        taskBool = true
        taskArray.push(t.name)
        idArray.push(t.value)
      }
    })
    if (taskBool) {
      this.picked_district = idArray
      this.spaceChangeByDistrict(idArray)
      this.expansion2_text = taskArray.toString()
    } else {
      this.picked_district = []
      this.space = [] // 
      this.expansion2_text = ''
    }
    this.district_task.completed = this.district_task.subtasks != null && taskBool;
  }

  // 
  spaceChangeByDistrict(idArray) {
    console.log(idArray);
    const a = idArray.map(x => { return this.idSource[x] })
    console.log(a);
    this.space = a.reduce((pre, c) => [...pre, ...c],
      [])

  }

  // id dialog
  openDialogId() {

    console.log(this.space);
    const dialogRef = this.dialog.open(DialogIdComponent, {
      // dialog
      disableClose: true,
      width: '60vw',
      maxHeight: '80vh',
      data: {
        source: this.source,
        space: this.space,
      },
    });
    // id dialog
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
      this.use_district = true
      result.forEach(e => {
        // console.log(e);
        if (!e.completed) {
          // idfalse 
          this.use_district = false
        }
      });
      if (this.use_district) {
        const taskArray = []
        const idArray = []
        this.district_task.subtasks.map(t => {
          if (t.completed) {
            taskArray.push(t.name)
            idArray.push(t.value)
          }
        })
        this.picked_district = idArray
        this.spaceChangeByDistrict(idArray)
        this.expansion2_text = taskArray.toString()
      } else {
        switch (this.source) {
          case 0:
            this.expansion2_text = ''
            break;
          case 1:
            this.expansion2_text = ''
            break;
          case 2:
            this.expansion2_text = ''
            break;
          case 3:
            this.expansion2_text = ''
            break;
        }
      }
    });

  }
