  // 
  yearDPHandlerS(normalizedYear: moment.Moment, datepicker: MatDatepicker<moment.Moment>) {
    const ctrlValue = this.selectdate.value;
    ctrlValue.year(moment(normalizedYear).year());
    // SQL
    // this.selected.startDate = moment(ctrlValue).startOf('year').toDate();
    // input
    this.selectYearS = ctrlValue.startOf('year').toDate();
    console.log('this.selectYearS', this.selectYearS);
    this.selectdate.setValue(ctrlValue);
    datepicker.close();
    // 
    // console.log(this.selected);
    // this.selectedDateEmitter.emit({ selected: this.selected, holidayBool: this.holidayBool, dateTabIdx: this.selectedIndex });
  }
  yearDPHandlerE(normalizedYear: moment.Moment, datepicker: MatDatepicker<moment.Moment>) {
    const ctrlValue = this.selectdate.value;
    ctrlValue.year(moment(normalizedYear).year());
    // SQL
    // this.selected.endDate = moment(ctrlValue).endOf('year').toDate();
    // input
    this.selectYearE = ctrlValue.endOf('year').toDate();
    this.selectdate.setValue(ctrlValue);
    datepicker.close();
    // 
    // this.selectedDateEmitter.emit({ selected: this.selected, holidayBool: this.holidayBool, dateTabIdx: this.selectedIndex });
  }
  /*  */
  Submit() {
    // token
    this.centerService.post('/api/users/get_session', {}).subscribe({
      // next: () => {

      // },
      error: () => {
        this.snackBar.open('', '', {
          duration: 10000,
          horizontalPosition: 'center',
          verticalPosition: 'top',
        });
        // 
        this.router.navigate([this.routers.LOGIN]).then();
      },
    });
    // tab
    let start;
    let end
    // 
    if (this.timeLabel == 0) {
      start = this.selectYearS
      end = this.selectYearE
      console.log(start, end);
      // 
    } else if (this.timeLabel == 1) {
      start = this.selectMonthStart
      end = this.selectMonthEnd
      console.log(start, end);
      //  or 
    } else if (this.timeLabel == 2 || this.timeLabel == 3) {
      start = this.selected.startDate
      end = this.selected.endDate
      console.log(start, end);

    }

    // id
    let all_id = []
    this.use_district = true
    if (this.space_index === 0) {
      this.space.forEach(e => {
        // console.log(e);
        if (e.completed) {
          all_id.push(e.value.v)
        } else {
          // idfalse 
          this.use_district = false
        }
      });
    } else if (this.space_index === 1) {
      all_id = this.space_id
    }
    console.log(all_id);
    if (all_id.length === 0) {
      this.snackBar.open('', '', {
        horizontalPosition: 'center',
        verticalPosition: 'top',
      });
      return
    }
    if (this.source === 0) {
      // 
      let is_day;
      // 
      let revenue;
      // 
      if (this.timeLabel == 3) {
        this.hourSelect = this.hourSelect_onstreet
        this.hourArray = []

        // hour
        for (let i = this.hourSelect.start; i <= this.hourSelect.end; i++) {
          this.hourArray.push(i.toString())
        }
        is_day = "false"
        revenue = "false"

        // 
      } else if (this.timeLabel == 2) {
        this.hourArray = []
        // hour
        for (let i = 8; i <= 21; i++) {
          this.hourArray.push(i.toString())
        }
        is_day = "true"
        revenue = "true"
      } else {
        this.hourArray = []
        // hour
        for (let i = 8; i <= 21; i++) {
          this.hourArray.push(i.toString())
        }
        is_day = "false"
        revenue = "true"
      }
      if (this.space_index == 1) {
        // console.log(all_id);
        this.centerService.fillter_multi_id$.next({
          "start": moment(start).format('YYYY-MM-DD HH:mm:ss.SSS'),
          "end": moment(end).format('YYYY-MM-DD HH:mm:ss.SSS'),
          "hour": this.hourArray, //
          "id": all_id,
          "holidays": this.holiday_type,
          "is_day": is_day,
          "revenue": revenue,
        })
      } else {
        this.centerService.fillter_multi$.next({
          "start": moment(start).format('YYYY-MM-DD HH:mm:ss.SSS'),
          "end": moment(end).format('YYYY-MM-DD HH:mm:ss.SSS'),
          "hour": this.hourArray, //
          "district": this.picked_district, //
          "holidays": this.holiday_type,
          "is_day": is_day,
          "revenue": revenue,
        })
      }


    } else if (this.source === 1) {
      // 
      console.log(this.timeLabel);
      if (this.timeLabel == 3) {
        this.hourSelect = this.hourSelect_sd
        this.hourArray = []
        // hour
        for (let i = this.hourSelect.start; i <= this.hourSelect.end; i++) {
          this.hourArray.push(i.toString())
        }
      } else {
        this.hourArray = []
        // hour
        for (let i = 8; i <= 23; i++) {
          this.hourArray.push(i.toString())
        }
      }
      if (this.space_index == 0 && this.use_district) {
        // 

        this.centerService.fillter_sd_district$.next({
          "hour": this.hourArray, //
          "district": this.picked_district, //
          "holidays": this.holiday_type,
        })
      } else {
        // ID
        this.centerService.fillter_sd_id$.next({
          "hour": this.hourArray, //
          "id": all_id, //
          "holidays": this.holiday_type,
        })
      }

    } else if (this.source === 2) {
      //  
      if (this.timeLabel == 2 || this.timeLabel == 3) {
        if (this.timeLabel == 2) {
          this.hourSelect = this.hourSelect_offstreet
          this.hourArray = []
          // hour
          for (let i = 0; i <= 23; i++) {
            this.hourArray.push(i.toString())
          }

        } else if (this.timeLabel == 3) {
          this.hourArray = []
          // hour
          for (let i = this.hourSelect.start; i <= this.hourSelect.end; i++) {
            this.hourArray.push(i.toString())
          }
        }
        this.centerService.fillter_offStreet$.next({
          "start": moment(start).format('YYYY-MM-DD HH:mm:ss.SSS'),
          "end": moment(end).format('YYYY-MM-DD HH:mm:ss.SSS'),
          "id": all_id, //
          "hour": this.hourArray, //
          "holidays": this.holiday_type,
          "revenue": "false",
        })
      } else {
        this.hourArray = []
        // hour
        for (let i = 0; i <= 23; i++) {
          this.hourArray.push(i.toString())
        }
        this.centerService.fillter_offStreet$.next({
          "start": moment(start).format('YYYY-MM-DD HH:mm:ss.SSS'),
          "end": moment(end).format('YYYY-MM-DD HH:mm:ss.SSS'),
          "id": all_id, //
          "hour": this.hourArray, //
          "holidays": this.holiday_type,
          "revenue": "true",
        })
      }

    } else if (this.source === 3) {
      // 
      if (this.timeLabel == 3) {
        this.hourSelect = this.hourSelect_onstreet
        this.hourArray = []
        for (let i = this.hourSelect.start; i <= this.hourSelect.end; i++) {
          this.hourArray.push(i.toString())
        }
        this.centerService.fillter_onStreet$.next({
          "start": moment(start).format('YYYY-MM-DD HH:mm:ss.SSS'),
          "end": moment(end).format('YYYY-MM-DD HH:mm:ss.SSS'),
          "id": all_id, //
          "hour": this.hourArray, //
          "holidays": this.holiday_type,
        })
      } else {
        this.hourArray = []
        // hour
        for (let i = 8; i <= 21; i++) {
          this.hourArray.push(i.toString())
        }
        this.centerService.fillter_onStreet_days$.next({
          "start": moment(start).format('YYYY-MM-DD HH:mm:ss.SSS'),
          "end": moment(end).format('YYYY-MM-DD HH:mm:ss.SSS'),
          "id": all_id, //
          "hour": this.hourArray, //
          "holidays": this.holiday_type,
        })
      }

    }

  }
