  sourceChange(item) {

    console.log(item.value);
    this.expansion1_text = item.name;
    this.source = item.value
    // this.centerService.mapSelectType$.next(-1)
    this.centerService.mapSelectType$.next(this.source)

    this.centerService.mapSelectType$.next(-999)
    this.centerService.mapSelectType$.next(-99)
    if (item.value === 1) {
      this.minDate = this.minDate_1
      for (let i = 0; i < this.district_task.subtasks.length; i++) {
        // 
        if (this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == "" ||
          this.district_task.subtasks[i].name == ""

        ) {
          this.district_task.subtasks[i].disabled = false
        } else {
          this.district_task.subtasks[i].disabled = true
          this.district_task.subtasks[i].completed = false
        }

      }

      this.hourSelect = this.hourSelect_sd
      this.selectableHour = this.selectableHour_sd
      this.idSource = this.sd_id
    } else if (item.value === 2) {
      this.minDate = this.minDate_2
      this.hourSelect = this.hourSelect_offstreet
      this.selectableHour = this.selectableHour_offstreet
      this.idSource = this.off_street_id
      for (let i = 0; i < this.district_task.subtasks.length; i++) {
        this.district_task.subtasks[i].disabled = false
      }
    }
    else if (item.value === 3) {
      this.minDate = this.minDate_3
      this.hourSelect = this.hourSelect_onstreet
      this.selectableHour = this.selectableHour_onstreet
      this.idSource = this.on_street_id
      for (let i = 0; i < this.district_task.subtasks.length; i++) {
        this.district_task.subtasks[i].disabled = false
      }
    }
    else if (item.value === 0) {
      this.minDate = this.minDate_0
      this.hourSelect = this.hourSelect_onstreet
      this.selectableHour = this.selectableHour_onstreet
      this.idSource = this.on_street_id
      for (let i = 0; i < this.district_task.subtasks.length; i++) {
        this.district_task.subtasks[i].disabled = false
      }
    }
    //  id
    const taskArray = []
    const idArray = []
    let count = 0
    this.district_task.subtasks.map(t => {
      if (t.completed) {
        taskArray.push(t.name)
        idArray.push(t.value)
      }
    })
    this.picked_district = idArray
    this.spaceChangeByDistrict(idArray)
    // 
    // this.clearMapSelection()
    this.space.map(id => {

      if (!id.completed) {
        count++
        switch (this.source) {
          case 0:
            this.expansion2_text = ''
            break;
          case 1:
            this.expansion2_text = ''
            break;
          case 2:
            this.expansion2_text = ''
            break;
          case 3:
            this.expansion2_text = ''
            break;
        }
      }
    }
    )
    //  id
    if (count == 0 && taskArray.length != 0) {
      this.expansion2_text = taskArray.toString()
    }
  }
