import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
// angular 
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
// Material 
import { MatCardModule } from '@angular/material/card';
import { MatIconModule } from '@angular/material/icon';
import { MatButtonModule } from '@angular/material/button';
import { MatMenuModule } from '@angular/material/menu';
import { MatSnackBarModule } from "@angular/material/snack-bar";
import { MatDialogModule } from '@angular/material/dialog';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatTreeModule } from '@angular/material/tree';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatExpansionModule } from '@angular/material/expansion';
import { MatTabsModule } from '@angular/material/tabs';
import { MatInputModule } from '@angular/material/input';
import { MatRadioModule } from '@angular/material/radio';
import { MatProgressBarModule } from '@angular/material/progress-bar';
import { MatPaginatorModule } from '@angular/material/paginator';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatNativeDateModule, MAT_DATE_FORMATS, MAT_DATE_LOCALE} from '@angular/material/core';
import { MatSelectModule } from '@angular/material/select';
import { MatListModule } from '@angular/material/list';
// 
import { registerLocaleData } from '@angular/common';
import localezh from '@angular/common/locales/zh';
// 
import { TreeviewModule } from 'ngx-treeview';
// leaflet
import { LeafletModule } from '@asymmetrik/ngx-leaflet';
// google-map
import { AgmCoreModule } from '@agm/core';
// echarts
import { NgxEchartsModule } from 'ngx-echarts';
// googleAPIKey
import { environment } from 'src/environments/environment';
import { LayoutsModule } from 'src/app/layouts/layouts.module';

// 
import { MainMapComponent, D_SelectionMeter } from './main-map/main-map.component';
import { FillterComponent } from './fillter/fillter.component'
import { ParkingInfoComponent } from './parking-info/parking-info.component';
import { ParkingEstimateComponent } from './parking-estimate/parking-estimate.component';
import { DialogIdComponent } from './fillter/components/dialog-id/dialog-id.component';
import { ParkingKpiComponent } from './parking-kpi/parking-kpi.component';
import { SearchComponent } from './search/search.component';



export const TW_FORMATS = {
  parse: {
    dateInput: 'YYYY-MM-DD',
  },
  display: {
    dateInput: 'YYYY-MM-DD',
    monthYearLabel: 'MMM YYYY',
    dateA11yLabel: 'LL',
    monthYearA11yLabel: 'MMMM YYYY',
  },
};
registerLocaleData(localezh);

@NgModule({
  declarations: [
    MainMapComponent,
    FillterComponent,
    D_SelectionMeter,
    ParkingInfoComponent,
    DialogIdComponent,
    ParkingEstimateComponent,
    ParkingKpiComponent,
    SearchComponent,

  ],
  providers: [
    { provide: MAT_DATE_LOCALE, useValue: 'zh-TW' },
    { provide: MAT_DATE_FORMATS, useValue: TW_FORMATS }
  ],
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    MatCardModule,
    MatIconModule,
    MatButtonModule,
    MatMenuModule,
    MatSnackBarModule,
    MatDialogModule,
    MatCheckboxModule,
    MatTreeModule,
    MatAutocompleteModule,
    MatFormFieldModule,
    MatTooltipModule,
    MatExpansionModule,
    MatTabsModule,
    MatInputModule,
    MatRadioModule,
    MatProgressBarModule,
    MatPaginatorModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatSelectModule,
    MatListModule,
    LeafletModule,
    LayoutsModule,
    TreeviewModule.forRoot(),
    NgxEchartsModule.forRoot({
      echarts: () => import('echarts')
    }),
    AgmCoreModule.forRoot({
      apiKey: environment.googleAPIKey
    })
  ],
  exports: [
    MainMapComponent,
    FillterComponent,
    D_SelectionMeter,
    ParkingInfoComponent,
    ParkingEstimateComponent,
    ParkingKpiComponent,
    SearchComponent,
  ]
})
export class SharedComponentsModule { }
