import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { Task } from '../../fillter.interface'
import { PageEvent } from '@angular/material/paginator';

@Component({
  selector: 'app-dialog-id',
  templateUrl: './dialog-id.component.html',
  styleUrls: ['./dialog-id.component.scss']
})
export class DialogIdComponent implements OnInit {

  constructor(
    private snackBar: MatSnackBar,
    public dialogRef: MatDialogRef<DialogIdComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) {
    switch (this.data.source) {
      case 1:
        this.name = ''
        break;
      case 2:
        this.name = ''
        break;
      case 3:
        this.name = ''
        break;
      default:
        this.snackBar.open('', '', {
          horizontalPosition: 'center',
          verticalPosition: 'top',
        });
        break;
    }
    this.id_task.subtasks = this.data.space
    this.id_length = this.data.space.length
    this.filter_id_task.subtasks = this.data.space
  }

  name:string = '';
  id_length:number= 0;
  // id
  id_task: Task = {
    name: '',
    completed: false,
    subtasks: []
  };
  //
  filter_id_task: Task = {
    name: '',
    completed: false,
    subtasks: []
  };
  pageIndex = 0;
  // MatPaginator Output
  filterValue:string = '';
  pageEvent(e){

    this.pageIndex = e.pageIndex
  }


  ngOnInit(): void {}

  // 
  setAll(completed: boolean) {
    this.id_task.completed = completed;
    if (this.id_task.subtasks == null) {
      return;
    }
    const taskArray = []
    const idArray = []
    this.id_task.subtasks.map(t => {
      t.completed = completed
      if (completed) {
        taskArray.push(t.name)
        idArray.push(t.value)
      }
    })
  }
  // 
  setCompleted(i: number) {
    this.id_task.subtasks[i].completed = !this.id_task.subtasks[i].completed
  }
  //id
  filter(value) {
    if(value) {
      this.id_task.subtasks =  this.filter_id_task.subtasks.filter((item) => {
          return item.name.includes(value);
      })
    } else {
      this.id_task.subtasks = this.data.space
    }
    this.id_length = this.id_task.subtasks.length
  }
  onNoClick(): void {
    this.dialogRef.close( this.id_task.subtasks);
  }
}
